.float{
    position:fixed;
    width:100px;
    height:60px;
    bottom:60px;
    right:30px;
    background-color:#00A298;
    color:#FFF;
    border-radius:50px;
    text-align:center;
    box-shadow: 2px 2px 3px #999;
    z-index: 999;
    display: flex;
    cursor: pointer;
    transition: transform .2s;
}

.zwicon-info-circle {
    color: #fff;
    margin: auto;
    width: 60px;
    height: auto;
    font-size: 35px;
}

.float:hover {
    transform: scale(1.1);
}
